@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto Condensed', sans-serif;;
}

.App{
    width:70%;
    margin: 50px auto;
    background: linear-gradient(180deg, #00D1FF 0%, rgba(157, 255, 249, 0.64) 100%);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
}

h1{
    font-size: 40px;
    padding: 15px 0;
}

header{
    width: 95%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: auto;
}

header h2{
    padding: 10px;
    font-weight: 100;
}

nav a{
    text-decoration: none;
    padding: 10px;
}

table{
    width: 95%;
    margin: auto;
    border-collapse: collapse;
    margin-top: 10px;
    margin-bottom: 30px;
    overflow-x: auto;
}

td, th{
    padding: 5px;
    text-align: left;
    border-bottom: 1px solid white;
}

form{
    margin: auto;
    display: flex;
    flex-direction: column;
    text-align: left;
    width: 65%;
    padding-bottom: 50px;
}

label, input, textarea{
    margin: 10px;
}

.formend{
    display: flex;
    flex-direction: row;
    justify-content:space-between;
}

.deadlinebox{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

#create{
    width: 100px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

